@import '../../themify.scss';
@import '../../theme.scss';
.favoritesList {
    position: fixed;
    display: flex;
    align-items: center;
    top: 0.2em;
    box-sizing: border-box;
    height: 50px;

    ul {
        padding: 0;
        display: flex;
    }
    .thumb {
        margin: 0.2em 0;
        border-radius: 50%;
        max-width: 48px;
        max-height: 48px;
        display: block;
    }
}
